<template>

  <div class="cdcontent" >

      <div class="dtop">{{model.order_id}}</div>


      <!--  -->
      <div class="dcontpass" >
        <label>{{model.glyys_realname}}:
          <i v-if="model.glyys_status=='1'"></i>
          <i v-else-if="model.glyys_status=='-1'"></i>
          <i v-else></i>
        </label> <br>
        <label></label> <i>{{model.glyys_creatime}}</i> <br>
        <label></label> {{model.glyys_remark}} <br>
      </div>

      <div class="dcontpass" >
        <label>{{model.gcbys_realname}}:
          <i v-if="model.gcbys_status=='1'"></i>
          <i v-else-if="model.gcbys_status=='-1'"></i>
          <i v-else></i>
        </label> <br>
        <label></label> <i>{{model.gcbys_creatime}}</i> <br>
        <label></label> {{model.gcbys_remark}} <br>
      </div>


      <div class="dcont" >
        <cube-textarea v-model="value"  :placeholder="placeholder" :autofocus="autofocus" :maxlength="maxlength"></cube-textarea>
      </div>

      <div class="dcont" >
        <cube-button class="cbbot"  :primary="true"  @click="showAlert(1)"></cube-button>
      </div>

      <div class="dcont" >
        <cube-button class="cbbot"  @click="showAlert(-1)"></cube-button>
      </div>



  </div>

</template>


<script>
export default {

  data() {
    return {
      model:{},
      value: '',
      placeholder: '',
      maxlength: 1000,
      autofocus: true,
    };
  },
  created() {
    /**url */
    var id=this.$route.query.id
    console.log("",id)

    //
    this.getInfo(id);
    
  },
  methods: {
    /*** 
     * 
    */
    getInfo(id) {
      var that=this;
      this.axios.post( "http://wxgzh.liuzhouchuyun.com/chedetails",
          this.qs.stringify({
            id: id,
          }))
        .then(
          function(res) {
            console.log("",res.data.data);
            that.model=res.data.data;

        });
    },

    /** */
    showAlert(sh) {
      var that=this;
       that.axios.post("http://wxgzh.liuzhouchuyun.com/checkAccDec",
          that.qs.stringify({
            id: that.model.id,
            openid:window.sessionStorage.getItem('openid'), //  
            position:window.sessionStorage.getItem('position'), //  
            ifgcb:window.sessionStorage.getItem('ifgcb'), // 
            realname:window.sessionStorage.getItem('realname'), //
            value:that.value,
            sh: sh,
          }),
        ).then(
          function(res) {
            console.log("",res.data);
            //
            if(res.data.code==200){
              //
              
              that.$createDialog({
                type: 'alert',
                icon: 'cubeic-alert',
                title: '',
                confirmBtn: {
                  text: '',
                  active: true,
                  disabled: false,
                  href: 'javascript:;'
                },
                onConfirm: () => {
                  that.$router.go(-1);
                },
              }).show()

            }else{
              const toast = that.$createToast({
                txt: res.data.msg,
                time: 3000,
                type:'error',
                mask:true,
              })
              toast.show();
            }
        });
    },

  }
};
</script>

<style lang="stylus" >

.titletop{
  text-align :left;
  width :90%;
  margin-left :5%;
  margin-bottom :20px;
}

.stac{
  font-size:10pt;
  line-height :15pt;
}

.nop{
  color: #E64340;
}

.alp{
  color: #0DBC79;
}

.inp{
  color: #C66D3B;
}

.cube-btn{
  width:90% !important;
  margin-left : 5% !important;
}


.cdcontent {
  background-color: #fafafa;
  display: flex !important;
  flex-direction: column !important;
  padding: 0px 0px !important;
  border-bottom :20px solid #fafafa !important;
}

.dtop {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left: 5%;
  padding-right: 5%;
}

.dcont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 5%;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.dcontpass{
  width: 90%;
  padding: 10px 0px 10px 5%;
  border-left: 5px solid #FC9153;
  font-size :10pt;
  margin-top :20px;
  text-align:left;
  line-height :20pt;
}

.dcontpass label{
  font-weight :bold;
}

</style>


