<template>


  <div class="ucontent" >
      <div class="utop" >

          <div class="nop" v-if="model.check=='-2'">
            
          </div>
          <div class="nop" v-else-if="model.check=='-1'">
            
          </div>
          <div class="alp" v-else-if="model.check=='1'">
            
          </div>
          <div class="inp" v-else>
            
          </div>

          <!-- <div class="ucent">{{model.time}}</div> -->
      </div>

      <div class="ucont" >
        <label></label><br>
        <label><i>{{model.gly_creatime}}</i>{{model.gly_remark}}</label><br>
        <label><i>{{model.fzr_creatime}}</i>{{model.fzr_remark}}</label><br>
        <label><i>{{model.gcb_creatime}}</i>{{model.gcb_remark}}</label><br>
        <label><i>{{model.aqk_creatime}}</i>{{model.aqk_remark}}</label><br>
      </div>

      <div v-if="model.gly_status=='0'||model.check=='-1'">
        <div class="dcont" >
          <cube-button class="cbbot"  @click="Edit"></cube-button>
        </div>
        <div class="dcont" v-if="model.check=='0'">
          <cube-button class="cbbot" :primary="true" @click="Cancel"></cube-button>
        </div>
      </div>

  </div>

</template>


<script>
export default {

  data() {
    return {
        model:{},
    };
  },
  mounted:function () {
    /**url */
    var _this=this
    var id=_this.$route.query.id
    console.log("",id)
    this.getInfo(id);
  },
  methods: {
    /*** 
     * 
    */
    getInfo(id) {
      var that=this;
      this.axios.post( "http://wxgzh.liuzhouchuyun.com/uetails",
          this.qs.stringify({
            id: id,
          }))
        .then(
          function(res) {
            console.log("",res.data.data);
            that.model=res.data.data;
        });
    },

    /** */
    Cancel() {
      var that=this;
       that.axios.post("http://wxgzh.liuzhouchuyun.com/useCancel",
          that.qs.stringify({
            id: that.model.id,
          }),
        ).then(
          function(res) {
            console.log("",res.data);
            //

            that.$createDialog({
                type: 'alert',
                icon: 'cubeic-alert',
                title: '',
                confirmBtn: {
                  text: '',
                  active: true,
                  disabled: false,
                  href: 'javascript:;'
                },
                onConfirm: () => {
                  that.getInfo(that.model.id);
                },
            }).show()

        });
    },

    /** */
    Edit() {
      this.$router.push({ path: "useele", query: { id: this.model.id } });
    },

  }
};
</script>

<style lang="stylus" >

.titletop{
  text-align :left;
  width :90%;
  margin-left :5%;
  margin-bottom :20px;
}

.stac{
  font-size:10pt;
  line-height :15pt;
}

.nop{
  color: #E64340;
}

.alp{
  color: #0DBC79;
}

.inp{
  color: #C66D3B;
}

.cube-btn{
  width:90% !important;
  margin-left : 5% !important;
}


.ucontent {
  background-color: #ffffff;
  display: flex !important;
  flex-direction: column !important;
  padding: 0px 0px !important;
  border-bottom :20px solid #fafafa !important;
}

.utop {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left: 5%;
  padding-right: 5%;
}

.ucont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 5%;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.ucontent .ucent {
  font-size: 10pt;
  color: #CCCCCC;
}

.dcont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 5%;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.dcontent .dcent {
  font-size: 10pt;
  color: #CCCCCC;
}
</style>


