<template>
<div>
  <div class="fontsa">
      <div class="apply itop"></div>

      <div class="thep" @click="zxApply">
        <img class="pic ijs" src="../assets/zxsq.png">
        <label></label>
      </div>

      <div class="thep" @click="bxApply">
        <img class="pic ils" src="../assets/bxsq.png">
        <label></label>
      </div>

      <div class="thep" @click="ydApply">
        <img class="pic ivs" src="../assets/ydsq.png">
        <label></label>
      </div>

      <div class="thep" @click="zxRecord">
        <img class="pic ihs" src="../assets/zxjl.png">
        <label></label>
      </div>

      <div class="thep" @click="bxRecord">
        <img class="pic izs" src="../assets/bxjl.png">
        <label></label>
      </div>

      <div class="thep" @click="ydRecord">
        <img class="pic ios" src="../assets/ydjl.png">
        <label></label>
      </div>

      <div class="thep" @click="zxAccept">
        <img class="pic ixs" src="../assets/ysjl.png">
        <label></label>
      </div>

  </div>


  <div class="fontsa" v-if="staff==1">
    <div class="apply itop"></div>

    <div class="thep" @click="zxXuncha">
      <img class="pic ijs" src="../assets/xc.png">
      <label></label>
    </div>

    <div class="thep" @click="bxFankui">
      <img class="pic ils" src="../assets/fk.png">
      <label></label>
    </div>
  </div>

  <div class="fontsa ibom" v-if="staff==1&&(position!=0||ifgcb==1||ifaqk==1)">
    <div class="apply itop"></div>

    <div class="thep" @click="zxCheck">
      <img class="pic ijs" src="../assets/zxsh.png">
      <label></label>
    </div>

    <div class="thep" @click="bxCheck">
      <img class="pic ils" src="../assets/bxsh.png">
      <label></label>
    </div>

    <div class="thep" @click="ydCheck">
      <img class="pic ivs" src="../assets/ydsh.png">
      <label></label>
    </div>

    <div class="thep" @click="ysCheck">
      <img class="pic izs" src="../assets/ys.png">
      <label></label>
    </div>


  </div>

</div>

</template>

<script>

export default {
  data() {
    return {
      staff:0,
      ifgcb:0,
      ifaqk:0,
      position:0,
    };
  },
  created () {
      //openid
      this.getUserInfo();

          //
          // var that=this;
          // window.sessionStorage.setItem('openid', "oX0621BFlnSkN5SlghGgRvQHik60") //openid
          // window.sessionStorage.setItem('staff', 1) // 
          // window.sessionStorage.setItem('own', 2) // 
          // window.sessionStorage.setItem('realname', "") // 
          // window.sessionStorage.setItem('position', 1) //   
          // window.sessionStorage.setItem('dname', "") //    
          // window.sessionStorage.setItem('ifgcb', 0) // 
          // window.sessionStorage.setItem('ifaqk', 0) //   
          
          // that.staff = 1;
          // that.ifgcb = 0;
          // that.ifaqk = 0;
          // that.position = 1;
  },
  methods: {

    getUserInfo() {
        var that=this;

        /**
       * 
       * 1. 
       */
       if (!window.sessionStorage.getItem('openid')) {
        // 
        if (!that.getUrlKey('code') || !that.getUrlKey('state')) {           // code code
         // 
         // const URI = encodeURIComponent(window.location.href)
         var URI = ''
            var APPID = 'wxde6f061ac193e843'                        // APPID
            //url
         if (window.location.href.indexOf('?') > 0) {  
          URI = encodeURIComponent(window.location.href)
         } else {
          URI = encodeURIComponent(window.location.href.split('?')[0])
         }

            console.log("",URL);
            //url
         var url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + APPID + '&redirect_uri=' + URI + '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
         window.location.href = url
        } else {   // codecodeuidoppind
              
              console.log("code",that.getUrlKey('code'))
              that.axios.get(
                "/testgetUserInfo",
                {
                  params: {
                    code: that.getUrlKey('code')
                  }  
                }
              )
              .then(
                function(res) {
                  console.log("userinfo:",res.data.data,"------openid:",res.data.data.openid);
                  that.getALLinfo(res.data.data.openid); //
                 
              });
              
        }
       }else{
            
          that.getALLinfo(window.sessionStorage.getItem('openid'));
            
      }
    },

    getUrlKey (variable) {
       var query = window.location.search.substring(1);
       var vars = query.split("&");
       for (var i=0;i<vars.length;i++) {
               var pair = vars[i].split("=");
               if(pair[0] == variable){return pair[1];}
       }
       return(false);
    },

    getALLinfo(openid){
      var that=this;
      const toast = that.$createToast({
        time: 0,
        txt: ''
      })
      toast.show();//
      that.axios.get(
        "/getAllInfo",
        {
          params: {
            openid: openid
          }  
        }
      )
      .then( res =>{
          window.sessionStorage.setItem('openid', res.data.data.openid) //openid
          window.sessionStorage.setItem('staff', res.data.data.staff) // 
          window.sessionStorage.setItem('own', res.data.data.own) // 
          window.sessionStorage.setItem('realname', res.data.data.realname) // 
          window.sessionStorage.setItem('position', res.data.data.position) //   
          window.sessionStorage.setItem('dname', res.data.data.dname) //    
          window.sessionStorage.setItem('ifgcb', res.data.data.ifgcb) // 
          window.sessionStorage.setItem('ifaqk', res.data.data.ifaqk) //   
          
          that.staff = res.data.data.staff;
          that.ifgcb = res.data.data.ifgcb;
          that.ifaqk = res.data.data.ifaqk;
          that.position = res.data.data.position;
          toast.hide()//
      })
    },


    /** */
    zxApply() {
      this.$router.push({ name: "Home" });
    },
    /** */
    bxApply() {
      this.$router.push({ name: "Repair" });
    },
    /** */
    ydApply() {
      this.$router.push({ name: "Useele" });
    },
    /** */
    zxRecord() {
      this.$router.push({ name: "Drecord" });
    },
    /** */
    bxRecord() {
      this.$router.push({ name: "Rrecord" });
    },
    /** */
    ydRecord() {
      this.$router.push({ name: "Urecord" });
    },
    /** */
    zxXuncha() {
      this.$router.push({ name: "Dlist" });
    },
    /** */
    bxFankui() {
      this.$router.push({ name: "Rlist" });
    },
    /** */
    zxAccept(){
      this.$router.push({ name: "Daccept" });
    },
    /** */
    zxCheck(){
      this.$router.push({ name: "Chedec" });
    },
    /** */
    bxCheck(){
      this.$router.push({ name: "Cherep" });
    },
    /** */
    ydCheck(){
      this.$router.push({ name: "Cheuse" });
    },
    /** */
    ysCheck(){
      this.$router.push({ name: "Acceptdec" });
    }
    
  }
};
</script>

<style lang="stylus">
.fontsa {
  font-size :8pt;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  border-bottom :1px solid #EEEEEE;
}

.pic {
  width: 48px;
  height: 48px;
  border-radius: 10%;
  margin-bottom :10px;
}

.ijs{
  background-color: #FFAE27;
}

.ils{
  background-color: #61CBFF;
}

.ihs{
  background-color: #DB98FF;
}

.izs{
  background-color: #00E4A7;
}

.ivs{
  background-color: #99DF2E;
}

.ios{
  background-color: #FF87AB;
}

.ixs{
  background-color: #FDDC69;
}

.thep{
  width :25%;
  display :flex;
  flex-direction :column;
  justify-content :center;
  align-items :center;
  margin-bottom :30px;
}


.apply{
  width :90%;
  font-size :14pt;
  font-weight :bold;
  margin-bottom :20px;
  text-align :left;
  padding-left :20px;
  line-height :24pt;
  color :#4A4C5B;
}

.itop{
  margin-top :30px;
}

.ibom{
  padding-bottom :50px;
}
</style>


